.text-primary {
  color: var(--primary);
}

.text-secondary {
  color: var(--secondary);
}

.text-warning {
  color: var(--warning);
}

.decoration-primary {
  text-decoration-color: var(--primary);
}

.filter {
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}

.transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

/*  */

:root {
  --primary: #335cff;
  --primaryLight1: #edf1ff;
  /*  */
  --primaryLight2: #e1e7ff;
  --secondary: #1c1c1c;
  --secondaryLight1: #7a7a7a;
  --warning: #fb3748;
  --warningLight1: #ffebec;
  --lightAccent1: #ffffff;
  /*  */
  --lightAccent2: #f9fafb;
  --lightAccent3: #ededed;
  --category-purple: #7d52f4;
  --category-blue: #47c2ff;
  --category-darkBlue: #335cff;
  --category-pink: #df7aca;
  --category-green: #22d3bb;
}

* {
  transition: all 0.4s;
}

/* progess */

progress {
  -webkit-appearance: none;
  appearance: none;
}

progress::-webkit-progress-bar {
  background-color: var(--lightAccent3);
  /*  */
}

progress::-webkit-progress-value {
  background-color: var(--category-color);
}

progress::-moz-progress-bar {
  background-color: var(--category-color);
}

/*  */

small {
  font-size: var(--font-size-sm);
}

/* section */

section {
  gap: 2rem;
  display: flex;
}

/* table */

/* theadtbody */

table tbody::before {
  content: "";
  display: block;
  margin-top: 12px;
}

table thead {
  border-bottom: 1px solid #d1d1d1;
}

table tbody tr td {
  padding-left: 1rem;
  padding-right: 1rem;
}

table td,
th {
  padding-top: 0.35rem;
  padding-bottom: 0.35rem;
  text-align: center;
  white-space: nowrap;
}

table tbody tr:nth-child(odd) {
  background-color: var(--primaryLight1);
}

table td:first-child {
  text-align: left;
}

/* button */

button,
.btn-base-oval {
  transition-duration: 0.3s;
  transition-property: transform;
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

button:hover,
button:active,
button:focus,
a:hover,
.btn-base-oval:hover,
.btn-base-oval:active,
.btn-base-oval:focus {
  transform: scale(1.1);
}

.categoryBtn {
  position: relative;
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}

.categoryBtn:before {
  content: "";
  position: absolute;
  border: 4px solid var(--category-color);
  border-radius: 50px;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition-duration: 0.4s;
  transition-property: top, right, bottom, left;
}

.categoryBtn:hover:before,
.categoryBtn:focus:before,
.categoryBtn:active:before {
  top: -8px;
  right: -8px;
  bottom: -8px;
  left: -8px;
  transition-duration: 0.4s;
}

.btn-base-rec {
  background-color: var(--secondary);
  color: var(--lightAccent1);
  border-radius: 0.25rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-primary-gradient {
  background: 
  /*  border 12%  0% */ linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.2) 0%,
        rgba(255, 255, 255, 0) 12%
      )
      border-box,
    /*  */
      linear-gradient(
        180deg,
        rgba(255, 255, 255, 0.2) 0%,
        rgba(255, 255, 255, 0) 75%
      ),
    var(--primary);
  background-clip: border-box, padding-box, padding-box;
  background-origin: border-box;
  border: 1px solid transparent;
  color: var(--lightAccent1);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-primary {
  background-color: var(--primary);
  color: var(--lightAccent1);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-primary-stroke {
  border: 1px solid var(--primary);
  color: var(--primary);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-neytral-stroke {
  border: 1px solid #d1d1d1;
  color: #7b7b7b;
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-warning-rec {
  background-color: var(--warning);
  color: var(--lightAccent1);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
}

.btn-warning-stroke {
  border: 1px solid var(--warning);
  color: var(--warning);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
}

.btn-warning-stroke-sqa {
  border: 1px solid var(--warning);
  color: var(--warning);
  border-radius: 10px;
  padding: 0.5rem;
}

.btn-warning-lighter {
  background-color: var(--warningLight1);
  color: var(--warning);
  border-radius: 10px;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  display: flex;
}

.btn-warning-squ {
  background-color: var(--warning);
  color: var(--lightAccent1);
  border-radius: 0.5rem;
  padding: 0.5rem;
}

/* border */

.border-dashed {
  background: linear-gradient(to right, #7a7a7a 50%, transparent 0) top,
    linear-gradient(to right, #7a7a7a 50%, transparent 0) bottom,
    linear-gradient(to bottom, #7a7a7a 50%, transparent 0) left,
    linear-gradient(to bottom, #7a7a7a 50%, transparent 0) right;
  background-size: 12px 1px, 12px 1px, 1px 12px, 1px 12px;
  background-repeat: repeat-x, repeat-x, repeat-y, repeat-y;
}

/* input */

.input-primary {
  width: 100%;
  border-radius: 0.5rem;
  border-width: 1.5px;
  border-color: var(--primaryLight2);
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

.input-primary::placeholder {
  --tw-text-opacity: 1;
  color: rgb(209 213 219 / var(--tw-text-opacity));
}

/* card */

.categoryCard {
  width: calc(33% - 1.25rem);
}

/* chart */

.chart {
  width: 100%;
  max-width: 24rem;
  /*  */
  height: auto;
}

.c3-chart-arcs-title {
  font-size: 2rem;
  font-size: var(--font-size-base);
  fill: var(--secondary);
}

.c3-legend-item {
  display: block;
  /*  */
}

.c3-legend-item text {
  font-size: 0.75rem;
  /*  */
}

/* note */

.note {
  background-color: #f6ec86;
  padding: 20px;
  min-width: 280px;
  height: 250px;
}

/* rwd */

@media screen and (max-width: 1024px) {
  .sec1 {
    width: calc(50% - 1.25rem);
  }
}

@media screen and (max-width: 800px) {
  .categoryCard {
    width: calc(50% - 1.25rem);
  }

  .footer-pic {
    height: 140px;
  }
}

@media screen and (max-width: 640px) {
  .sec1 {
    width: 100%;
  }

  .categoryCard {
    width: 100%;
  }

  .background {
    height: 2.5rem !important;
  }

  .footer-pic {
    height: 100px;
  }

  h2 {
    font-size: 1.5rem !important;
  }
}

@media (min-width: 640px) {
  .sm\:absolute {
    position: absolute;
  }

  .sm\:-bottom-12 {
    bottom: -3rem;
  }

  .sm\:-left-12 {
    left: -3rem;
  }

  .sm\:mt-8 {
    margin-top: 2rem;
  }

  .sm\:block {
    display: block;
  }

  .sm\:flex-row {
    flex-direction: row;
  }

  .sm\:items-start {
    align-items: flex-start;
  }

  .sm\:whitespace-nowrap {
    white-space: nowrap;
  }
}

@media (min-width: 800px) {
  .md\:block {
    display: block;
  }

  .md\:w-\[420px\] {
    width: 420px;
  }

  .md\:max-w-\[400px\] {
    max-width: 400px;
  }

  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }

  .md\:flex-row {
    flex-direction: row;
  }

  .md\:items-start {
    align-items: flex-start;
  }

  .md\:items-center {
    align-items: center;
  }

  .md\:text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  .md\:text-6xl {
    font-size: 3.75rem;
    line-height: 1;
  }
}

@media (min-width: 1024px) {
  .lg\:my-16 {
    margin-top: 4rem;
    margin-bottom: 4rem;
  }

  .lg\:flex-row {
    flex-direction: row;
  }

  .lg\:justify-start {
    justify-content: flex-start;
  }

  .lg\:text-3xl {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
}

@media (min-width: 1200px) {
  .xl\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
